import React, { useState } from "react";
import { Container, Form, Button, Alert } from "react-bootstrap";
import { parse } from "papaparse";

const CSVToSRTConverter = () => {
  const [csvFile, setCsvFile] = useState(null);
  const [csvFileName, setCsvFileName] = useState("");
  const [error, setError] = useState("");
  const [srtContent, setSrtContent] = useState("");
  const [hasHeaders, setHasHeaders] = useState(false);
  const [startTimeHeader, setStartTimeHeader] = useState("");
  const [endTimeHeader, setEndTimeHeader] = useState("");
  const [textHeader, setTextHeader] = useState("");

  const handleFileChange = (e) => {
    const file = e.target.files[0];
    setCsvFile(file);
    if (file) {
      const name = file.name.replace(/\.[^/.]+$/, ""); // Remove the file extension
      setCsvFileName(name);
    }
  };

  const handleConvert = () => {
    if (!csvFile) {
      setError("Please upload a CSV file.");
      return;
    }

    if (hasHeaders && (!startTimeHeader || !endTimeHeader || !textHeader)) {
      setError("Please provide custom header names.");
      return;
    }

    parse(csvFile, {
      header: hasHeaders,
      complete: (results) => {
        const srtData = results.data;
        const srtString = hasHeaders
          ? convertCSVToSRTWithHeaders(srtData)
          : convertCSVToSRTWithoutHeaders(srtData);
        setSrtContent(srtString);
      },
      error: () => {
        setError("Error parsing CSV file.");
      },
    });
  };

  const convertCSVToSRTWithHeaders = (data) => {
    let srt = "";
    data.forEach((row, index) => {
      if (row[startTimeHeader] && row[endTimeHeader] && row[textHeader]) {
        srt += `${index + 1}\n`;
        srt += `${row[startTimeHeader]} --> ${row[endTimeHeader]}\n`;
        srt += `${row[textHeader]}\n\n`;
      }
    });
    return srt;
  };

  const convertCSVToSRTWithoutHeaders = (data) => {
    let srt = "";
    data.forEach((row, index) => {
      // Ensure the row has at least 3 columns
      if (row.length >= 3) {
        srt += `${index + 1}\n`;
        srt += `${row[0]} --> ${row[1]}\n`;
        srt += `${row[2]}\n\n`;
      }
    });
    return srt;
  };

  const handleDownload = () => {
    const blob = new Blob([srtContent], { type: "text/plain;charset=utf-8" });
    const link = document.createElement("a");
    link.href = URL.createObjectURL(blob);
    link.download = `${csvFileName}.srt`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  return (
    <Container>
      <h1 className="mt-5">CSV to SRT Converter</h1>
      <Form>
        <Form.Group controlId="formFile" className="mb-3">
          <Form.Label>Upload CSV File</Form.Label>
          <Form.Control type="file" accept=".csv" onChange={handleFileChange} />
        </Form.Group>
        <Form.Group controlId="formBasicCheckbox">
          <Form.Check
            type="checkbox"
            label="CSV has headers"
            checked={hasHeaders}
            onChange={(e) => setHasHeaders(e.target.checked)}
          />
        </Form.Group>
        {hasHeaders && (
          <>
            <Form.Group controlId="formStartTimeHeader" className="mt-2">
              <Form.Label>Start Time Header</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter the header name for start time"
                value={startTimeHeader}
                onChange={(e) => setStartTimeHeader(e.target.value)}
              />
            </Form.Group>
            <Form.Group controlId="formEndTimeHeader" className="mt-2">
              <Form.Label>End Time Header</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter the header name for end time"
                value={endTimeHeader}
                onChange={(e) => setEndTimeHeader(e.target.value)}
              />
            </Form.Group>
            <Form.Group controlId="formTextHeader" className="my-2">
              <Form.Label>Text Header</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter the header name for text"
                value={textHeader}
                onChange={(e) => setTextHeader(e.target.value)}
              />
            </Form.Group>
          </>
        )}
        {error && <Alert variant="danger">{error}</Alert>}
        <Button variant="primary" onClick={handleConvert}>
          Convert to SRT
        </Button>
        {srtContent && (
          <Button variant="success" className="ms-3" onClick={handleDownload}>
            Download SRT
          </Button>
        )}
      </Form>
    </Container>
  );
};

export default CSVToSRTConverter;
